/**
 * Main script for converting Pikachu Volleyball replay files to video
 * Real-time recording with MediaRecorder at 30 FPS
 * WITH EXTENSIVE DEBUGGING
 */
'use strict';
import { settings } from '@pixi/settings';
import { SCALE_MODES } from '@pixi/constants';
import { Renderer, BatchRenderer, autoDetectRenderer } from '@pixi/core';
import { Prepare } from '@pixi/prepare';
import { Container } from '@pixi/display';
import { Loader } from '@pixi/loaders';
import { SpritesheetLoader } from '@pixi/spritesheet';
import { Ticker } from '@pixi/ticker';
import { CanvasRenderer } from '@pixi/canvas-renderer';
import { CanvasSpriteRenderer } from '@pixi/canvas-sprite';
import { CanvasPrepare } from '@pixi/canvas-prepare';
import '@pixi/canvas-display';
import { ASSETS_PATH } from '../offline_version_js/assets_path.js';
import { PikachuVolleyballReplay } from './pikavolley_replay.js';
import { setGetSpeechBubbleNeeded, enableChat } from '../chat_display.js';
import { serialize } from '../utils/serialize.js';
import { getHashCode } from '../utils/hash_code.js';
import '../../style.css';

console.log('üé¨ [INIT] main_out.js loaded');

/**
 * Replay to Video Converter with MediaRecorder (Real-time recording)
 */
class ReplayToVideoConverter {
  constructor() {
    console.log('üé¨ [CONSTRUCTOR] Creating ReplayToVideoConverter');
    
    // Setup PIXI
    Renderer.registerPlugin('prepare', Prepare);
    Renderer.registerPlugin('batch', BatchRenderer);
    CanvasRenderer.registerPlugin('prepare', CanvasPrepare);
    CanvasRenderer.registerPlugin('sprite', CanvasSpriteRenderer);
    Loader.registerPlugin(SpritesheetLoader);
    settings.RESOLUTION = 2;
    settings.SCALE_MODE = SCALE_MODES.NEAREST;
    settings.ROUND_PIXELS = true;

    this.renderer = null;
    this.stage = null;
    this.loader = null;
    this.ticker = null;
    this.pikaVolley = null;
    this.currentPack = null;
    
    this.mediaRecorder = null;
    this.recordedChunks = [];
    this.isRecording = false;
    
    console.log('‚úÖ [CONSTRUCTOR] ReplayToVideoConverter created');
  }

  updateStatus(message) {
    const statusEl = document.getElementById('status');
    console.log(`üìù [STATUS] ${message}`);
    if (statusEl) {
      statusEl.textContent = message;
    }
  }

  handleRecordButtonClick() {
    console.log('üîò [BUTTON] Record button clicked');
    
    if (this.isRecording) {
      console.warn('‚ö†Ô∏è [BUTTON] Already recording!');
      this.updateStatus('Ïù¥ÎØ∏ Ï≤òÎ¶¨ Ï§ëÏûÖÎãàÎã§...');
      return;
    }

    console.log('üìÇ [BUTTON] Opening file picker...');
    const fileInput = document.createElement('input');
    fileInput.type = 'file';
    fileInput.accept = '.txt';
    fileInput.onchange = (e) => {
      // @ts-ignore
      const file = e.target.files[0];
      console.log('üìÑ [FILE] File selected:', file?.name);
      if (file) {
        this.loadReplayFile(file);
      }
    };
    fileInput.click();
  }

  loadReplayFile(file) {
    console.log('üì• [LOAD] Loading replay file:', file.name);
    this.updateStatus('Î¶¨ÌîåÎ†àÏù¥ ÌååÏùº Î°úÎî© Ï§ë...');
    
    const recordBtn = document.getElementById('record-btn');
    console.log('üîò [LOAD] Record button element:', recordBtn);
    // @ts-ignore
    if (recordBtn) recordBtn.disabled = true;

    // Show loading overlay
    const loadingOverlay = document.getElementById('loading-overlay');
    console.log('‚è≥ [LOAD] Loading overlay element:', loadingOverlay);
    // @ts-ignore
    if (loadingOverlay) {
      loadingOverlay.classList.remove('hidden');
      console.log('‚úÖ [LOAD] Loading overlay shown');
    }

    const reader = new FileReader();
    reader.onload = (event) => {
      console.log('üìñ [LOAD] File read complete');
      try {
        // @ts-ignore
        const packWithComment = JSON.parse(event.target.result);
        console.log('üì¶ [LOAD] Pack parsed:', {
          hasNicknames: !!packWithComment.pack?.nicknames,
          nicknamesLength: packWithComment.pack?.nicknames?.length,
          nicknames: packWithComment.pack?.nicknames,
          hasIPs: !!packWithComment.pack?.partialPublicIPs,
          IPs: packWithComment.pack?.partialPublicIPs,
          hasChats: !!packWithComment.pack?.chats,
          chatsLength: packWithComment.pack?.chats?.length,
        });
        
        const pack = packWithComment.pack;
        const hash = pack.hash;
        pack.hash = 0;
        if (hash !== getHashCode(serialize(pack))) {
          throw new Error('ÌååÏùº ÎÇ¥Ïö©Ïù¥ Ìï¥Ïãú ÏΩîÎìúÏôÄ ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§');
        }
        console.log('‚úÖ [LOAD] Hash verification passed');
        this.initializeRenderer(pack);
      } catch (err) {
        console.error('‚ùå [LOAD] Error loading file:', err);
        // @ts-ignore
        this.updateStatus('ÌååÏùº Î°úÎî© Ïã§Ìå®: ' + err.message);
        // @ts-ignore
        if (recordBtn) recordBtn.disabled = false;
        // @ts-ignore
        if (loadingOverlay) loadingOverlay.classList.add('hidden');
      }
    };
    reader.onerror = () => {
      console.error('‚ùå [LOAD] File read error');
      this.updateStatus('ÌååÏùº ÏùΩÍ∏∞ Ïã§Ìå®');
      // @ts-ignore
      if (recordBtn) recordBtn.disabled = false;
      // @ts-ignore
      if (loadingOverlay) loadingOverlay.classList.add('hidden');
    };
    reader.readAsText(file);
  }

  initializeRenderer(pack) {
    console.log('üé® [RENDERER] Initializing renderer...');
    this.updateStatus('Í≤åÏûÑ ÏóêÏÖã Î°úÎî© Ï§ë...');
    this.currentPack = pack;

    // Setup renderer
    this.renderer = autoDetectRenderer({
      width: 432,
      height: 304,
      antialias: false,
      backgroundColor: 0x000000,
      backgroundAlpha: 1,
      forceCanvas: true,
      preserveDrawingBuffer: true,
    });
    console.log('‚úÖ [RENDERER] Renderer created');

    this.stage = new Container();
    this.ticker = new Ticker();
    this.ticker.minFPS = 1;
    this.loader = new Loader();
    console.log('‚úÖ [RENDERER] Stage, ticker, loader created');

    // Append canvas to container
    const container = document.querySelector('#game-canvas-container');
    console.log('üì¶ [RENDERER] Game container element:', container);
    // @ts-ignore
    container.innerHTML = '';
    // @ts-ignore
    container.appendChild(this.renderer.view);
    console.log('‚úÖ [RENDERER] Canvas appended to container');

    // Setup assets
    const TEXTURES = ASSETS_PATH.TEXTURES;
    TEXTURES.WITH_COMPUTER = TEXTURES.WITH_FRIEND;

    this.loader.add(ASSETS_PATH.SPRITE_SHEET);
    for (const prop in ASSETS_PATH.SOUNDS) {
      this.loader.add(ASSETS_PATH.SOUNDS[prop]);
    }
    console.log('‚úÖ [RENDERER] Assets added to loader');

    this.setupLoaderProgressBar();
    this.loader.load(() => this.setupGame(pack));
    console.log('üîÑ [RENDERER] Loading assets...');
  }

  setupLoaderProgressBar() {
    const progressBar = document.getElementById('progress-bar');
    console.log('üìä [PROGRESS] Progress bar element:', progressBar);
    if (progressBar) {
      this.loader.onProgress.add(() => {
        // @ts-ignore
        progressBar.style.width = `${this.loader.progress}%`;
      });
    }
  }

  setupGame(pack) {
    console.log('üéÆ [GAME] Setting up game...');
    console.log('üì¶ [GAME] Pack data:', {
      nicknames: pack.nicknames,
      IPs: pack.partialPublicIPs,
      chatsCount: pack.chats?.length,
      inputsCount: pack.inputs?.length,
    });
    this.updateStatus('Í≤åÏûÑ Ï¥àÍ∏∞Ìôî Ï§ë...');

    // Check DOM elements BEFORE creating game
    console.log('üîç [DOM] Checking DOM elements...');
    const chatBox1 = document.getElementById('player1-chat-box');
    const chatBox2 = document.getElementById('player2-chat-box');
    const nick1 = document.getElementById('player1-nickname');
    const nick2 = document.getElementById('player2-nickname');
    const ip1 = document.getElementById('player1-partial-ip');
    const ip2 = document.getElementById('player2-partial-ip');
    const chatDisabled1 = document.getElementById('player1-chat-disabled');
    const chatDisabled2 = document.getElementById('player2-chat-disabled');
    
    console.log('üìã [DOM] Chat boxes:', { chatBox1, chatBox2 });
    console.log('üìã [DOM] Nicknames:', { nick1, nick2 });
    console.log('üìã [DOM] IPs:', { ip1, ip2 });
    console.log('üìã [DOM] Chat disabled:', { chatDisabled1, chatDisabled2 });

    console.log('üéÆ [GAME] Creating PikachuVolleyballReplay...');
    this.pikaVolley = new PikachuVolleyballReplay(
      this.stage,
      this.loader.resources,
      pack.roomID,
      pack.nicknames,
      pack.partialPublicIPs,
      pack.inputs,
      pack.options,
      pack.chats
    );
    console.log('‚úÖ [GAME] PikachuVolleyballReplay created');

    // Set speech bubble function
    console.log('üí¨ [GAME] Setting speech bubble function...');
    try {
      // @ts-ignore
      setGetSpeechBubbleNeeded(this.pikaVolley);
      console.log('‚úÖ [GAME] Speech bubble function set');
    } catch (err) {
      console.error('‚ùå [GAME] Error setting speech bubble:', err);
    }

    // Enable chat display
    console.log('üí¨ [GAME] Enabling chat display...');
    this.pikaVolley.willDisplayChat = true;
    try {
      enableChat(true);
      console.log('‚úÖ [GAME] Chat enabled');
    } catch (err) {
      console.error('‚ùå [GAME] Error enabling chat:', err);
    }

    // Enable nickname and IP display by checking checkboxes programmatically
    console.log('‚òëÔ∏è [CHECKBOX] Activating checkboxes...');
    const showNicknamesCheckbox = document.getElementById('show-nicknames-checkbox');
    const showIPsCheckbox = document.getElementById('show-ip-addresses-checkbox');
    const showChatCheckbox = document.getElementById('show-chat-checkbox');
    
    console.log('üìã [CHECKBOX] Checkbox elements:', {
      nicknames: showNicknamesCheckbox,
      ips: showIPsCheckbox,
      chat: showChatCheckbox,
    });
    
    if (showNicknamesCheckbox) {
      console.log('‚òëÔ∏è [CHECKBOX] Activating nicknames checkbox...');
      // @ts-ignore
      showNicknamesCheckbox.checked = true;
      showNicknamesCheckbox.dispatchEvent(new Event('change'));// @ts-ignore
      console.log('‚úÖ [CHECKBOX] Nicknames checkbox activated, checked:', showNicknamesCheckbox.checked);
    } else {
      console.warn('‚ö†Ô∏è [CHECKBOX] Nicknames checkbox not found!');
    }
    
    if (showIPsCheckbox) {
      console.log('‚òëÔ∏è [CHECKBOX] Activating IPs checkbox...');
      // @ts-ignore
      showIPsCheckbox.checked = true;
      showIPsCheckbox.dispatchEvent(new Event('change'));// @ts-ignore
      console.log('‚úÖ [CHECKBOX] IPs checkbox activated, checked:', showIPsCheckbox.checked);
    } else {
      console.warn('‚ö†Ô∏è [CHECKBOX] IPs checkbox not found!');
    }
    
    if (showChatCheckbox) {
      console.log('‚òëÔ∏è [CHECKBOX] Activating chat checkbox...');
      // @ts-ignore
      showChatCheckbox.checked = true;
      showChatCheckbox.dispatchEvent(new Event('change'));// @ts-ignore
      console.log('‚úÖ [CHECKBOX] Chat checkbox activated, checked:', showChatCheckbox.checked);
    } else {
      console.warn('‚ö†Ô∏è [CHECKBOX] Chat checkbox not found!');
    }

    // Check DOM elements AFTER checkbox activation
    console.log('üîç [DOM] Checking DOM elements AFTER checkbox activation...');
    console.log('üìã [DOM] Nickname classes:', {
      nick1: nick1?.className,
      nick2: nick2?.className,
    });
    console.log('üìã [DOM] IP classes:', {
      ip1: ip1?.className,
      ip2: ip2?.className,
    });

    // Disable BGM completely
    console.log('üîá [AUDIO] Disabling BGM...');
    this.pikaVolley.audio.turnBGMVolume(false);
    if (this.pikaVolley.audio.sounds.bgm && this.pikaVolley.audio.sounds.bgm.center) {
      this.pikaVolley.audio.sounds.bgm.center.stop();
      // Override play method to prevent BGM
      // @ts-ignore
      this.pikaVolley.audio.sounds.bgm.center.play = function() {
        console.log('üîá [AUDIO] BGM play blocked');
      };
      // @ts-ignore
      this.pikaVolley.audio.sounds.bgm.center.isPlaying = false;
    }
    console.log('‚úÖ [AUDIO] BGM disabled');

    // Enable SFX (sound effects)
    console.log('üîä [AUDIO] Enabling SFX...');
    this.pikaVolley.audio.turnSFXVolume(true);
    console.log('‚úÖ [AUDIO] SFX enabled');

    // Setup ticker for rendering
    console.log('üîÑ [TICKER] Setting up ticker...');
    this.ticker.add(() => {
      this.renderer.render(this.stage);
      this.pikaVolley.gameLoop();
    });
    console.log('‚úÖ [TICKER] Ticker set up');

    setTimeout(() => this.startRecording(), 100);
    console.log('‚è∞ [GAME] Recording will start in 100ms');
  }

  startRecording() {
    console.log('üé• [RECORD] Starting recording...');
    this.updateStatus('ÎÖπÌôî Ï§ÄÎπÑ Ï§ë...');
    this.recordedChunks = [];
    this.isRecording = true;

    // @ts-ignore
    const canvas = this.renderer.view;
    console.log('üñºÔ∏è [RECORD] Canvas:', canvas);
    
    try {
      // Create canvas stream at 30 FPS
      const fps = 30;
      // @ts-ignore
      const canvasStream = canvas.captureStream(fps);
      console.log('üìπ [RECORD] Canvas stream created at', fps, 'FPS');

      // Setup MediaRecorder
      let mimeType = 'video/webm;codecs=vp9';
      if (!MediaRecorder.isTypeSupported(mimeType)) {
        console.warn('‚ö†Ô∏è [RECORD] VP9 not supported, trying VP8');
        mimeType = 'video/webm;codecs=vp8';
        if (!MediaRecorder.isTypeSupported(mimeType)) {
          console.warn('‚ö†Ô∏è [RECORD] VP8 not supported, using default webm');
          mimeType = 'video/webm';
        }
      }
      console.log('‚úÖ [RECORD] Using codec:', mimeType);

      this.mediaRecorder = new MediaRecorder(canvasStream, {
        mimeType: mimeType,
        videoBitsPerSecond: 2500000, // 2.5 Mbps
      });
      console.log('‚úÖ [RECORD] MediaRecorder created');

      this.mediaRecorder.ondataavailable = (e) => {
        if (e.data.size > 0) {
          this.recordedChunks.push(e.data);
          console.log(`üì¶ [RECORD] Data chunk received: ${e.data.size} bytes, total chunks: ${this.recordedChunks.length}`);
        }
      };

      this.mediaRecorder.onstop = () => {
        console.log('‚èπÔ∏è [RECORD] MediaRecorder stopped');
        this.saveVideoFile();
      };

      this.mediaRecorder.onerror = (e) => {
        console.error('‚ùå [RECORD] MediaRecorder error:', e);
        this.updateStatus('ÎÖπÌôî Ï§ë ÏóêÎü¨ Î∞úÏÉù');
        this.isRecording = false;
        const recordBtn = document.getElementById('record-btn');
        // @ts-ignore
        if (recordBtn) recordBtn.disabled = false;
      };

      // Start recording
      this.mediaRecorder.start(100); // Collect data every 100ms
      console.log('‚ñ∂Ô∏è [RECORD] MediaRecorder started');

      // Start game ticker at 30 FPS
      this.ticker.maxFPS = 30;
      this.ticker.start();
      console.log('‚ñ∂Ô∏è [TICKER] Ticker started at 30 FPS');

      this.updateStatus(`ÎÖπÌôî Ï§ë... 0%`);
      this.monitorProgress();
      console.log('üìä [RECORD] Progress monitoring started');

    } catch (err) {
      console.error('‚ùå [RECORD] Error starting recording:', err);
      // @ts-ignore
      this.updateStatus('ÎÖπÌôî ÏãúÏûë Ïã§Ìå®: ' + err.message);
      this.isRecording = false;
      const recordBtn = document.getElementById('record-btn');
      // @ts-ignore
      if (recordBtn) recordBtn.disabled = false;
    }
  }

  monitorProgress() {
    const totalFrames = this.pikaVolley.inputs.length;
    console.log('üìä [PROGRESS] Total frames:', totalFrames);
    const progressBar = document.getElementById('progress-bar');
    
    const checkInterval = setInterval(() => {
      if (!this.pikaVolley || !this.isRecording) {
        console.log('‚èπÔ∏è [PROGRESS] Stopping progress monitoring');
        clearInterval(checkInterval);
        return;
      }

      const currentFrame = this.pikaVolley.replayFrameCounter;
      const progress = Math.floor((currentFrame / totalFrames) * 100);
      
      if (currentFrame % 100 === 0) {
        console.log(`üìä [PROGRESS] Frame ${currentFrame}/${totalFrames} (${progress}%)`);
      }
      
      this.updateStatus(`ÎÖπÌôî Ï§ë... ${progress}%`);
      if (progressBar) {
        // @ts-ignore
        progressBar.style.width = `${progress}%`;
      }

      if (currentFrame >= totalFrames) {
        console.log('üèÅ [PROGRESS] All frames processed');
        clearInterval(checkInterval);
        this.stopRecording();
      }
    }, 500);
  }

  stopRecording() {
    if (!this.isRecording) {
      console.log('‚ö†Ô∏è [STOP] Not recording, skipping stop');
      return;
    }

    console.log('‚èπÔ∏è [STOP] Stopping recording...');
    this.updateStatus('ÎÖπÌôî ÏôÑÎ£å, ÌååÏùº Ï†ÄÏû• Ï§ë...');
    this.ticker.stop();
    console.log('‚èπÔ∏è [STOP] Ticker stopped');
    
    if (this.mediaRecorder && this.mediaRecorder.state !== 'inactive') {
      console.log('‚èπÔ∏è [STOP] Stopping MediaRecorder...');
      this.mediaRecorder.stop();
    } else {
      console.log('‚ö†Ô∏è [STOP] MediaRecorder already inactive, saving directly');
      this.saveVideoFile();
    }
  }

  saveVideoFile() {
    console.log('üíæ [SAVE] Saving video file...');
    console.log('üì¶ [SAVE] Recorded chunks:', this.recordedChunks.length);
    
    if (this.recordedChunks.length === 0) {
      console.error('‚ùå [SAVE] No recorded data!');
      this.updateStatus('ÎÖπÌôîÎêú Îç∞Ïù¥ÌÑ∞Í∞Ä ÏóÜÏäµÎãàÎã§');
      this.isRecording = false;
      const recordBtn = document.getElementById('record-btn');
      // @ts-ignore
      if (recordBtn) recordBtn.disabled = false;
      return;
    }

    const blob = new Blob(this.recordedChunks, { type: 'video/webm' });
    console.log('üì¶ [SAVE] Blob created:', blob.size, 'bytes');
    const url = URL.createObjectURL(blob);
    console.log('üîó [SAVE] Object URL:', url);
    
    const d = new Date();
    const dateStr = `${d.getFullYear()}${('0' + (d.getMonth() + 1)).slice(-2)}${('0' + d.getDate()).slice(-2)}`;
    const timeStr = `${('0' + d.getHours()).slice(-2)}${('0' + d.getMinutes()).slice(-2)}`;
    
    let filename = `${dateStr}_${timeStr}_pikavolley_replay.webm`;
    
    const pack = this.currentPack;
    if (pack && pack.nicknames && pack.nicknames.length === 2) {
      const nick1 = pack.nicknames[0].replace(/[/\\?%*:|"<>]/g, '_');
      const nick2 = pack.nicknames[1].replace(/[/\\?%*:|"<>]/g, '_');
      filename = `${dateStr}_${timeStr}_${nick1}_vs_${nick2}.webm`;
    }
    console.log('üìù [SAVE] Filename:', filename);

    const a = document.createElement('a');
    a.href = url;
    a.download = filename;
    a.style.display = 'none';
    document.body.appendChild(a);
    a.click();
    console.log('‚úÖ [SAVE] Download triggered');
    
    setTimeout(() => {
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
      console.log('üßπ [SAVE] Cleanup complete');
    }, 100);

    this.updateStatus('ÏôÑÎ£å! Îã§Î•∏ Î¶¨ÌîåÎ†àÏù¥Î•º Î≥ÄÌôòÌïòÎ†§Î©¥ Î≤ÑÌäºÏùÑ Îã§Ïãú ÌÅ¥Î¶≠ÌïòÏÑ∏Ïöî.');
    this.isRecording = false;
    const recordBtn = document.getElementById('record-btn');
    // @ts-ignore
    if (recordBtn) recordBtn.disabled = false;
    this.recordedChunks = [];
    
    // Hide loading overlay
    const loadingOverlay = document.getElementById('loading-overlay');
    // @ts-ignore
    if (loadingOverlay) loadingOverlay.classList.add('hidden');
    console.log('‚úÖ [SAVE] Complete!');
  }
}

// Adjust assets path
function adjustAssetsPath() {
  console.log('üîß [ASSETS] Adjusting assets path...');
  ASSETS_PATH.SPRITE_SHEET = '../' + ASSETS_PATH.SPRITE_SHEET;
  for (const prop in ASSETS_PATH.SOUNDS) {
    ASSETS_PATH.SOUNDS[prop] = '../' + ASSETS_PATH.SOUNDS[prop];
  }
  console.log('‚úÖ [ASSETS] Assets path adjusted');
}

adjustAssetsPath();

// Initialize converter
console.log('üé¨ [INIT] Initializing converter...');
const converter = new ReplayToVideoConverter();
const recordBtn = document.getElementById('record-btn');
console.log('üîò [INIT] Record button:', recordBtn);
if (recordBtn) {
  recordBtn.addEventListener('click', () => converter.handleRecordButtonClick());
  console.log('‚úÖ [INIT] Record button listener attached');
}

// Setup UI event listeners for nickname/IP/chat display
document.addEventListener('DOMContentLoaded', () => {
  console.log('üìÑ [DOM] DOMContentLoaded event fired');
  
  const showNicknamesCheckbox = document.getElementById('show-nicknames-checkbox');
  const showIPsCheckbox = document.getElementById('show-ip-addresses-checkbox');
  const showChatCheckbox = document.getElementById('show-chat-checkbox');
  
  const player1NicknameElem = document.getElementById('player1-nickname');
  const player2NicknameElem = document.getElementById('player2-nickname');
  const player1IPElem = document.getElementById('player1-partial-ip');
  const player2IPElem = document.getElementById('player2-partial-ip');

  console.log('üîç [DOM] UI Elements:', {
    showNicknamesCheckbox,
    showIPsCheckbox,
    showChatCheckbox,
    player1NicknameElem,
    player2NicknameElem,
    player1IPElem,
    player2IPElem,
  });

  if (showNicknamesCheckbox) {
    showNicknamesCheckbox.addEventListener('change', () => {// @ts-ignore
      console.log('‚òëÔ∏è [EVENT] Nicknames checkbox changed:', showNicknamesCheckbox.checked);
      // @ts-ignore
      if (showNicknamesCheckbox.checked) {
        if (player1NicknameElem) {
          player1NicknameElem.classList.remove('hidden');
          console.log('‚úÖ [EVENT] Player1 nickname shown');
        }
        if (player2NicknameElem) {
          player2NicknameElem.classList.remove('hidden');
          console.log('‚úÖ [EVENT] Player2 nickname shown');
        }
      } else {
        if (player1NicknameElem) player1NicknameElem.classList.add('hidden');
        if (player2NicknameElem) player2NicknameElem.classList.add('hidden');
      }
    });
    console.log('‚úÖ [DOM] Nicknames checkbox listener attached');
  }

  if (showIPsCheckbox) {
    showIPsCheckbox.addEventListener('change', () => {// @ts-ignore
      console.log('‚òëÔ∏è [EVENT] IPs checkbox changed:', showIPsCheckbox.checked);
      // @ts-ignore
      if (showIPsCheckbox.checked) {
        if (player1IPElem) {
          player1IPElem.classList.remove('hidden');
          console.log('‚úÖ [EVENT] Player1 IP shown');
        }
        if (player2IPElem) {
          player2IPElem.classList.remove('hidden');
          console.log('‚úÖ [EVENT] Player2 IP shown');
        }
      } else {
        if (player1IPElem) player1IPElem.classList.add('hidden');
        if (player2IPElem) player2IPElem.classList.add('hidden');
      }
    });
    console.log('‚úÖ [DOM] IPs checkbox listener attached');
  }

  if (showChatCheckbox) {
    showChatCheckbox.addEventListener('change', () => {
    // @ts-ignore
      console.log('‚òëÔ∏è [EVENT] Chat checkbox changed:', showChatCheckbox.checked);
      // @ts-ignore
      if (showChatCheckbox.checked) {
        console.log('üí¨ [EVENT] Enabling chat...');
        try {
          enableChat(true);
          console.log('‚úÖ [EVENT] Chat enabled');
        } catch (err) {
          console.error('‚ùå [EVENT] Error enabling chat:', err);
        }
      } else {
        console.log('üîá [EVENT] Disabling chat...');
        try {
          enableChat(false);
          console.log('‚úÖ [EVENT] Chat disabled');
        } catch (err) {
          console.error('‚ùå [EVENT] Error disabling chat:', err);
        }
      }
    });
    console.log('‚úÖ [DOM] Chat checkbox listener attached');
  }
});

